/* Navigation styling--------------------------------------->*/
.navigation{
  width: 100%;
  background:#7e002b;
}

.navigation .text-design,
.navigation .justify-content-end .text-design{
  color: white;
  padding: 10px 20px; 
  font-family: 'Ubuntu Condensed', sans-serif;
}

