.squareItem {
	margin-top: 30px;
    position: relative;
}

.squareInnerContainer {
	position: relative;
	padding: 50px 15px 35px;
	text-align: center;
	border: 2px solid #444;
	border-top: none;
}

.squareInnerContainer:before,
.squareInnerContainer:after {
	content: '';
	position: absolute;
	top: 0px;
	width: 30%;
	border-bottom: 2px solid #444;
}


.squareInnerContainer:before {
	right: 0px;
}

.squareInnerContainer:after {
	left: 0px
}


.squareContent h2 {
	font-size: 56px;
	margin-bottom: 10px;
	font-weight: 400;
	color: #5e003e;
}

.squareContent h3 {
	font-size: 13px;
	font-weight: 600;
    margin: 0;
    color: #5e003e;
}

.squareIcon {
	position: absolute;
    left: 50%;
    top: -30px;
    width: 40%;
    height: 60px;
    font-size: 45px;
    margin-left: -20%;
    color: #444;
}

.company img{
	width: 80%;
	height: 80%;
	object-fit: contain;
	overflow: hidden;
}
.sticky-l, .sticky-r {
    z-index: 150;
    max-width: 30%;
    min-height: 150px;
    margin: -10px 10px 10px;
    padding: 10px;
    font-family: "Gloria Hallelujah", "Comic Sans", "Chalkboard SE", "Comic Neue", cursive;
    font-size: 14px;
    color: #000;
    box-shadow: -2px 2px 2px rgba(0,0,0,0.3);
  }

  .sticky-l {
    -webkit-transform: rotate(-5deg);
            transform: rotate(-5deg);
  }

  .sticky-r {
    -webkit-transform: rotate(5deg);
            transform: rotate(5deg);
  }

  .sticky-l:before, .sticky-l:after, .sticky-r:before, .sticky-r:after {
    content: "";
    display: block;
    position: absolute;
    width: 16px;
    height: 16px;
    top: 0;
    right: 0;
  }
  .sticky-l:before, .sticky-r:before {
    border-top: solid 8px #fff;
    border-right: solid 8px #fff;
    border-left: solid 8px transparent;
    border-bottom: solid 8px transparent;
  }
  .sticky-l:after, .sticky-r:after {
    border-bottom: solid 8px #dddd33;
    border-left: solid 8px #dddd33;
    border-right: solid 8px transparent;
    border-top: solid 8px transparent;
  }
